.chat {
    display: flex;
  
    flex-direction: column;
    flex: 0.65;
  }
  .chat_header{
  
      padding:20px;
      display:flex;
      align-items:center;
      border-bottom:1px solid lightgray;
  }
  .chat_headerInfo{
      flex:1;
      padding-left:20px;
  }
  .chat_body{
    flex:1;
  
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAPoAAADKCAMAAAC7SK2iAAAAA1BMVEX///+nxBvIAAAASElEQVR4nO3BMQEAAADCoPVPbQ0PoAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAeDMYOAAEnHI17AAAAAElFTkSuQmCC');
  background-position: center;
  padding: 30px;
  overflow: scroll;
  background-repeat: repeat;
  }
  .chat_message{
    position: relative;
    font-size: 16px;
    padding:10px;
    width: fit-content;
    border-radius: 10px;
    background-color: #D0CBC4;
    margin-bottom: 30px;
  }
  .chat_timestamp{
    margin-left:10px;
    font-size: xx-small;
  }
  .chat_name{
    position: absolute;
    top:-15px;
    font-weight: 800;
    font-size: xx-small;
  
  }
  .chat_reciever{
    margin-left:auto;
    background-color: #7084ec;
  }
  .chat_footer{
    
    display:flex;
    justify-content: space-between;
  align-items: center;
  height:62px;
  border-top: 1px solid lightgray;
  
  }
  .chat_footer>form{
    flex:1;
    display:flex;
  }
  .chat_footer>form>input{
    flex:1;
    outline-width: 0;
    border-radius: 30px;
    padding: 10px;
    border:none;
  }
  .chat_footer>form>button{
    display:none
  }
  .chat_footer>.MuiSvgIcon-root{
    padding: 10px;
    color:gray;
  }